(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('GameController', GameController);

	/** @ngInject */
	function GameController($log, $state, studentService, Session) {
		var vm = this;
		var student;
		var sizeScenes;
		vm.history;
		vm.index = 0;
		vm.active = "activated";

		init();
		/** função principal que recupera todas questoes ainda não jogadas pelo aluno */
		function init() {
			studentService.validateProfile();
			student = Session.refreshUserSession();
			getHistoryByStudentId(student.id);
		}

		function getHistoryByStudentId(studentId) {
			studentService.getHistory(studentId).then(function(response) {
				vm.history = response;
				sizeScenes = vm.history.length;
				verifyGameIsActived();
			});
		}

		/** função que envia um resposta do aluno ao back-end */
		vm.sendAnswer = function(alternative) {
			var answerContext = {};
			answerContext.sceneIndex = vm.history[vm.index].index;
			answerContext.sceneId = vm.history[vm.index].id;
			answerContext.studentId = student.id;
			answerContext.skillId = vm.history[vm.index].question.skillId;
			answerContext.skillValue = alternative.skillValue;

			studentService.sendAnswer(answerContext).then(function(status) {
				if(status == 200) vm.nextScene();
			});
		}

		/** função que realiza a mudança de cena na tela */
		vm.nextScene = function() {
			$log.log(vm.index);
			/** caso chege ao fim das cenas, deve levar aos resultados */
			if((vm.index + 1) == sizeScenes) {
				$log.log('fim do jogo, vai pra resultados');
				$state.go('student.result');
				return;
			}
			vm.index = vm.index + 1;
			vm.background = {'background-image' : "url(" + vm.history[vm.index].background.filename + ")"};
			if(vm.history[vm.index].question != null) {
				vm.active = "inactive";
				return;
			}
			vm.active = "active";
		}
		/** funcção que ativa o som do mouse hover */
		vm.playhover = function () {
			var hover = document.getElementById("over");
			hover.currentTime = 0;
			hover.play();
		}

		vm.infoout = function () {
			$log.info('fora');
			var opic = document.getElementById("infobox");
			opic.className="infoclose";
		}

		var verifyGameIsActived = function () {
			if(student.isCompleted === true) {
				$state.go("^.result");
			} else if (sizeScenes === 0) {
				$state.go("^.noGame");
			} else {
				vm.background = {"background-image" : "url(" + vm.history[vm.index].background.filename +")"};
			}
		}

	}

})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('MentorThemeController', MentorThemeController);

	/** @ngInject */
	function MentorThemeController($log, toastrService, mentorService, loginService) {
		var vm = this;
		var themeIdCurrent;
		vm.themeCurrent;

		var init = function() {
			mentorService.validateProfile();
			loadThemeCurrent();
		}

		init();
/** coloca todos temas vindo do server como false, somente colocando como true
o que está habilitado para o instituição */
		function disableOthersThemes() {
			$log.log("função: disableOthersThemes, ID TEMA ATUAL => "+themeIdCurrent);
			var sizeArray = vm.allThemes.length;
			for(var i = 0; i < sizeArray; i++) {
				if(vm.allThemes[i].id == themeIdCurrent) vm.allThemes[i].active = true;
				else vm.allThemes[i].active = false;
			}
		}
/** carrega todos temas disponiveis que estão habilitados pelo administrador 	*/
		function loadAllThemes() {
			mentorService.loadAllThemesActivated().then(function(response) {
				vm.allThemes = response;
				disableOthersThemes();
			});
		}

		function loadThemeCurrent() {
			var institutionCode = loginService.getUserLogged().institutionCode;
			mentorService.loadThemeCurrent(institutionCode).then(function(response) {
				themeIdCurrent = response;
				loadAllThemes();
			});
		}

		vm.updateThemeIdCurrent = function() {
			var institutionCode = loginService.getUserLogged().institutionCode;
			mentorService.updateThemeIdCurrent(institutionCode, themeIdCurrent).then(function(status) {
				toastrService.showToastr(status);
			});
		}

/** manipula os switchs para que fique somente um ATIVO
ou todos DESATIVADOS  */
		vm.setThemeCurrent = function(theme) {
			if(themeIdCurrent == theme.id) {
				themeIdCurrent = 0;
			}
			else if(themeIdCurrent != 0) {
				vm.allThemes[themeIdCurrent - 1].active = false;
				themeIdCurrent = theme.id;
			}	else {
				themeIdCurrent = theme.id;
			}
		}

	}
})();

(function() {
	'use strict';

	angular
		.module('mapskillsWeb')
		.controller('MentorStudentController', MentorStudentController);

	/** @ngInject */
	function MentorStudentController($log, toastrService, mentorService, modalService) {
		var vm = this;
		vm.allStudents = null;

    init();

    function init() {
			mentorService.validateProfile();
			loadAllStudents(true);
			loadAllCourses();
      vm.student = mentorService.getObjectCurrent();
			$log.log(vm.student);
			mentorService.setObjectCurrent(null);
    }

		function loadAllStudents(loadFromServer) {
			mentorService.loadAllStudents(loadFromServer).then(function(response) {
				vm.allStudents = response;
			});
		}

		function loadAllCourses() {
			mentorService.loadAllCourses().then(function(response) {
				vm.allCourses = response;
			});
		}

    vm.openStudentModal = function(student) {
      mentorService.setObjectCurrent(student);
      modalService.openModal('/app/components/mentor/students/student.modal.html', 'MentorStudentController');
		}

		vm.openDetailsModal = function(student) {
      mentorService.setObjectCurrent(student);
      modalService.openModal('/app/components/mentor/students/details.modal.html', 'MentorStudentController');
		}

		vm.openFileModal = function() {
      modalService.openModal('/app/shared/modal/file.modal.html', 'MentorStudentController');
		}

    vm.saveStudent = function(student) {
			if(!validateStudent(student)) {
				toastrService.showToastr(400);
				return;
			}
      mentorService.saveStudent(student).then(function(status) {
				postVerify(status);
				loadAllStudents(true);
			});
    }

		vm.sendFile = function(file) {
      mentorService.sendFile(file).then(function(status) {
				postVerify(status);
			});
    }
/** verifica o status da requisição para o retorno
dos funções saveStudent e sendFile */
		function postVerify(status) {
			if(status == 200) {
				$log.log('status '+ status);
				vm.closeModal();
			}
			toastrService.showToastr(status);
		}

		function validateStudent(student) {
			if(angular.isUndefined(student) || student === null || angular.isUndefined(student.name) || angular.isUndefined(student.ra) || angular.isUndefined(student.username)) {
				return false;
			}
			if(student.name && student.ra.length === 13 && student.phone && student.username) {
				return true;
			}
		}

		vm.closeModal = function() {
			modalService.closeModal();
		}

	}

})();

